.hamburger {
  z-index: 200;
  background: transparent;
  border: none;
  height: 49px;
  width: 55px;
  -webkit-tap-highlight-color: #00000000;
}

#nav-icon2 {
  width: 60px;
  height: 45px;
  position: relative;
  -webkit-transform: rotate(0deg) scale(0.4);
  transform: rotate(0deg) scale(0.4);
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon2 span {
  display: block;
  position: absolute;
  height: 7px;
  width: 50%;
  background: #111111;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  z-index: 10;
}

#nav-icon2 span:nth-child(even) {
  left: 50%;
  border-radius: 0 9px 9px 0;
}

#nav-icon2 span:nth-child(odd) {
  left: 0px;
  border-radius: 9px 0 0 9px;
}

#nav-icon2 span:nth-child(1),
#nav-icon2 span:nth-child(2) {
  top: 0px;
}

#nav-icon2 span:nth-child(3),
#nav-icon2 span:nth-child(4) {
  top: 18px;
}

#nav-icon2 span:nth-child(5),
#nav-icon2 span:nth-child(6) {
  top: 36px;
}

#nav-icon2.hamburger--open span:nth-child(1),
#nav-icon2.hamburger--open span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

#nav-icon2.hamburger--open span:nth-child(2),
#nav-icon2.hamburger--open span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#nav-icon2.hamburger--open span:nth-child(1) {
  left: 5px;
  top: 7px;
}

#nav-icon2.hamburger--open span:nth-child(2) {
  left: calc(50% - 5px);
  top: 7px;
}

#nav-icon2.hamburger--open span:nth-child(3) {
  left: -50%;
  opacity: 0;
}

#nav-icon2.hamburger--open span:nth-child(4) {
  left: 100%;
  opacity: 0;
}

#nav-icon2.hamburger--open span:nth-child(5) {
  left: 5px;
  top: 29px;
}

#nav-icon2.hamburger--open span:nth-child(6) {
  left: calc(50% - 5px);
  top: 29px;
}

